import {useEffect, useContext} from 'react';
import { Link, Route, Switch, useRouteMatch } from 'react-router-dom';
import { useHistory } from 'react-router-dom';
import {useDispatch, useSelector} from 'react-redux';
import {LoadUser} from '../../application/middlewares/auth';
import {getToken} from '../../application/selectors/auth';
import Input from "@material-tailwind/react/Input";
import Button from "@material-tailwind/react/Button";
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { useForm, Controller} from "react-hook-form";
import { post } from '../../application/middlewares';
import { 
    setToken,loginSuccess
} from "../../application/actions/auth";
import { GetCookie, SetCookie, RemoveCookie } from '../../utils/cookie'
import logo1 from '../image/logo1.svg';
import fon_gamysh from '../image/fon_gamysh.svg'
import Lang1 from '../Language/Lang1';
import { LangContext } from "../Language/LangContext";



function VerifyCode(){
    const {  control, handleSubmit, formState: { errors }, setError} = useForm({
        // resolver: yupResolver(schema)
    });
    const dispatch = useDispatch();
  const { lang, dispatchLang } = useContext(LangContext);

    const token = useSelector(getToken);
    // console.log(token)
    // const isLogged = useSelector(getIsLogged);
    const history = useHistory();
    // console.log(token);
    // console.log(data);
    // useEffect(() => {
    //     if(isLogged === false){
    //         dispatch(LoadUser({history}));
    //     }
    //     // eslint-disable-next-line
    // }, [dispatch, isLogged]);


    const onSubmit = (data) => {
    dispatch(post({
        url:'user/verify-code',
        token: `${token}`,
        data,
        action:(response)=>{
            if(response.success){
                console.log("gelen verify",response.data)
                // dispatch(setToken(response.data.token));
                dispatch(loginSuccess({"token":response.data.token, "data":response.data.data}));
                SetCookie('refresh_token', response.data.refresh_token);
                history.push('/')
            }else {
                console.log(response);
                setError('code', { type:'manual', message:response?.message?.data?.error?.code});

            }
        },
    }))
}
    return(
        <div className="flex justify-center items-center h-full w-full fon_gamysh"
        style={{ backgroundImage: `url(https://gamysh.com/uploads/images/fon_gamysh.svg)` }}
        >
        <div className="flex max-w-sm mx-auto overflow-hidden rounded-lg shadow-lg dark:bg-gray-800 lg:w-80">
        <form onSubmit={handleSubmit(onSubmit)} className="w-full px-6 py-8 md:px-8">
        <div className='flex justify-center w-full my-5'>
            <img src='https://gamysh.com/uploads/images/logo1.svg' className='auth_img' />
            </div>
            <h2 className="text-2xl font-semibold text-center text-gray-700 dark:text-white my-5">{Lang1[lang].VerifyCode}</h2>
            {/* <p className="text-xl text-center text-gray-600 dark:text-gray-200">Welcome back!</p> */}
            <div className="mt-4 w-full">
                <Controller
                    control={control}
                    name="code"
                    render={({ field: { onChange, onBlur, value, ref } }) => {
                        return (
                            <Input
                            onChange={onChange} onBlur={onBlur} 
                           type="code"  size="regular" color="blue"
                           outline={true} placeholder="Code" error={errors.code?.message} />
                    )}}
                />
            </div>
            
           

            <div className="mt-8 w-full flex justify-end items-center">
            <Button
                color="lightBlue"
                buttonType="link"
                size="regular"
                rounded={false}
                block={false}
                iconOnly={false}
                ripple="dark"
            >
                {Lang1[lang].send}
            </Button>
            </div>
        </form>
    </div>
    </div>
    )
}

export default VerifyCode;